"use client";

import Image from "next/image";
import { useState } from "react";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogDescription,
} from "@/components/ui/dialog";

export default function FeatureCard({ feature }: { feature: any }) {
  const [open, setOpen] = useState(false);
  const [selectedService, setSelectedService] = useState<any>(null);

  const handleLearnMore = (service: any) => {
    setSelectedService(service);
    setOpen(true);
  };

  // Safely split data that comes as comma-separated text from your sheet
  const parseList = (field: string | undefined) =>
    field ? field.split(",").map((item) => item.trim()).filter(Boolean) : [];

  return (
    <>
      {/* Feature Card */}
      <div className="p-6 rounded-lg shadow bg-gradient-to-br from-sky-50 to-white hover:shadow-lg transition">
        <h3 className="text-xl font-semibold mb-2">{feature.Title}</h3>
        <p className="text-slate-600 mb-4">
          {feature.ShortDescription ?? feature.Intro ?? feature.Description}
        </p>
        <div
          className="text-blue-600 hover:underline cursor-pointer font-medium"
          onClick={() => handleLearnMore(feature)}
        >
          Learn More â†’
        </div>
      </div>

      {/* Modal */}
      <Dialog open={open} onOpenChange={setOpen}>
        <DialogContent className="max-w-2xl">
          {selectedService && (
            <>
              <DialogHeader>
                <DialogTitle className="text-2xl font-semibold">
                  {selectedService.Title}
                </DialogTitle>
                <DialogDescription>
                  {selectedService.Intro}
                </DialogDescription>
              </DialogHeader>

              {selectedService.HeroImage && (
                <Image
                  src={`/images/${selectedService.HeroImage}`}
                  alt={selectedService.Title}
                  width={800}
                  height={400}
                  className="rounded-lg my-4 object-cover w-full h-auto"
                />
              )}

              <div className="space-y-4 text-gray-700">
                {/* Key Points */}
                {selectedService.KeyPoints && (
                  <div>
                    <h4 className="font-semibold text-lg mb-1">Key Points</h4>
                    <ul className="list-disc pl-6">
                      {parseList(selectedService.KeyPoints).map((point, i) => (
                        <li key={i}>{point}</li>
                      ))}
                    </ul>
                  </div>
                )}

                {/* Process */}
                {selectedService.Process && (
                  <div>
                    <h4 className="font-semibold text-lg mb-1">Process</h4>
                    <ol className="list-decimal pl-6">
                      {parseList(selectedService.Process).map((step, i) => (
                        <li key={i}>{step}</li>
                      ))}
                    </ol>
                  </div>
                )}

                {/* Benefits */}
                {selectedService.Benefits && (
                  <div>
                    <h4 className="font-semibold text-lg mb-1">Benefits</h4>
                    <ul className="list-disc pl-6">
                      {parseList(selectedService.Benefits).map((b, i) => (
                        <li key={i}>{b}</li>
                      ))}
                    </ul>
                  </div>
                )}
              </div>
            </>
          )}
        </DialogContent>
      </Dialog>
    </>
  );
}
